body {
  padding-top: 20px; }

#myCarousel .nav {
  a small {}
  display: block;
  background: #eee; }

.nav-justified > li > a {
  border-radius: 0px; }

.nav-pills > li {
  &[data-slide-to="0"].active a {
    background-color: #16a085; }
  &[data-slide-to="1"].active a {
    background-color: #e67e22; }
  &[data-slide-to="2"].active a {
    background-color: #2980b9; }
  &[data-slide-to="3"].active a {
    background-color: #8e44ad; } }
